import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./CBTTestList.scss";

const testData = [
  {
    title: "  2024 3",
    subjects: [
      { name: " ", score: 19, total: 20, url: "/project/qualifications/infoProcessing/202403/1" },
      { name: " ", score: 16, total: 20, url: "/project/qualifications/infoProcessing/202403/1" },
      { name: " ", score: 19, total: 20, url: "/project/qualifications/infoProcessing/202403/1" },
      { name: " ", score: 19, total: 20, url: "/project/qualifications/infoProcessing/202403/1" },
      { name: " ", score: 20, total: 20, url: "/project/qualifications/infoProcessing/202403/1" }
    ],
  },
  {
    title: "  2024 2",
    subjects: [],
  },
  {
    title: "  2024 1",
    subjects: [],
  },
  {
    title: "  2023 3",
    subjects: [],
  },
  {
    title: "  2023 2",
    subjects: [],
  },
];

const CBTTestList = () => {
  const [openIndex, setOpenIndex] = useState(0);
  const navigate = useNavigate();

  const toggleOpen = (index) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  const handleClick = (url) => {
    navigate(url);
  };

  return (
      <div className="cbt-test-list">
      <h2 className="cbt-title"> CBT</h2>
        <h3></h3>
        <p>{testData.length}  .</p>
        {testData.map((test, index) => (
          <div className="test-item" key={index}>
            <button
              className={`test-title ${openIndex === index ? "open" : ""}`}
              onClick={() => toggleOpen(index)}
            >
              {test.title}
              <span className="score-badge">100</span>
              <span className="toggle">{openIndex === index ? "" : "+"}</span>
            </button>
            {openIndex === index && test.subjects.length > 0 && (
              <div className="test-subjects">
                <ul>
                  {test.subjects.map((subject, i) => (
                    <li key={i} onClick={() => handleClick(subject.url)}>
                      <span>{subject.name}</span>
                      <span className="score">
                        <span className="score-now">{subject.score}</span> / {subject.total}
                      </span>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
        ))}
      </div>
  );
};

export default CBTTestList;
